.intro {
  height: 90vh;
  // padding-top: 2em;  
  
  img {
    width: 260px;
    border-radius: 5%;
    margin: 5em 1em 1em 1em;
    filter: drop-shadow(5px 3px 0.9em #2a578a);
  }

  
}

.temp {
  display: flex;
  justify-content: space-between;
}

a {
  // text-decoration: none;
  &:visited {
    color: inherit;
  }
}

@media screen and (min-width: 820px) {
  .intro {
    display: flex;
    justify-content: space-around;
    align-items: center;

    img {
      width: 400px;
      height: 70%;
      margin: 1em;
    }


    > div {
      // height: 100%;
    }
  }
}